<template>
    <AccountLayout>
        <v-layout column align-center justify-center fill-height>
            <v-layout row wrap justify-center align-center>
                <v-flex xs10>
                    <h2>{{titleQuestion}}</h2>
                    <h2>{{title}}</h2>
                </v-flex>
                <v-flex xs10>
                    <FloorSelector @floorSelected="onFloorSelected" />
                </v-flex>
                <v-flex offset-xs6 xs4>
                    <v-btn flat color="primary" dark @click="save">Save</v-btn>
                </v-flex>
            </v-layout>
        </v-layout>
    </AccountLayout>
</template>
<script>
import AccountLayout from '@/components/layout/AccountLayout.vue'
import FloorSelector from '@/components/employee/FloorSelector.vue'
import { roles } from '@/common/roles'
import { mapActions, mapGetters } from 'vuex'
import router from '@/router'
import store from '@/store'

export default {
    components: {
        AccountLayout,
        FloorSelector
    },
    data: () => ({
        titleQuestion: 'Where Am I?',
        title: ' Choose your current floor',
        selectedFloorId: ''
    }),
    computed: {
        ...mapGetters('account', ['employeeId'])
    },
    methods: {
        ...mapActions('employee', ['updateEmployeeFloor']),
        onFloorSelected({ floorId }) {
            this.selectedFloorId = floorId
        },
        save() {
            this.updateEmployeeFloor({ floorId: this.selectedFloorId, employeeId: this.employeeId }).then(() => {
                const user = store.getters['account/user']

                if (user.roles.some(role => role == roles.officeAdministrator)) {
                    router.push({ name: 'office-management' })
                }

                if (user.roles.some(role => role == roles.employee || role == roles.guest)) {
                    router.push({ name: 'floor-map' })
                }
            })
        }
    }
}
</script>
<style lang="scss" scoped>
@import '@/styles/global.scss';
.stay-to-right {
    position: absolute;
    right: 25px;
}

.stay-to-bottom {
    position: absolute;
    bottom: 0px;
}

h2 {
    text-align: center;
    color: $primary-color-1;
}
</style>