<template>
    <v-layout align-center justify-center column fill-height>
        <v-layout row>&nbsp;</v-layout>
        <v-layout>
            <v-flex xs12>
                <v-layout row>
                    <v-btn
                        class="white--text big-button"
                        large
                        @click="onEmployeeClicked"
                    >Endavan Sign In</v-btn>
                </v-layout>

                <v-divider />

                <v-layout row>
                    <v-btn
                        class="big-button"
                        large
                        outline
                        color="primary"
                        @click="onGuestClicked"
                    >Continue As Guest</v-btn>
                </v-layout>
            </v-flex>
        </v-layout>
    </v-layout>
</template>

<script>
import '@/compiled-icons/guest'
import '@/compiled-icons/employee'
import { mapActions } from 'vuex'

export default {
    methods: {
        ...mapActions('account', ['loginGuest', 'loginEmployee']),
        onGuestClicked() {
            this.loginGuest()
        },
        onEmployeeClicked() {
            this.loginEmployee()
        }
    }
}
</script>

<style lang="scss">
@import '@/styles/global.scss';

.reddish-background {
    background-color: $reddish !important;
}

.login-text {
    font-family: Roboto;
    font-size: 35px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #000000;
}

.big-button {
    @include big-button($primary-color-1);
}
</style>

